import { Injectable, Inject } from '@angular/core';
import { Router, NavigationEnd, NavigationError } from '@angular/router';
import { AnalyticsService } from '../analytics/analytics.service';
import { DataStorageService } from '../data-storage/data-storage.service';
import { AnalyticsBean } from '../../analytics-bean/analytics-bean';
import { DOCUMENT } from '@angular/platform-browser';
import { EventLabels } from '../../types/event.types';
declare let ngS3AnalyticsJS: any;
@Injectable({
  providedIn: 'root'
})
export class RouterService {
  analyticsData: AnalyticsBean;
  eventLabels = EventLabels;
  navigateOn = '';
  constructor(private routes: Router, private analyticsService: AnalyticsService, private dataStorage: DataStorageService,
    // tslint:disable-next-line: deprecation
    @Inject(DOCUMENT) private document: any) {

  }

  /**
   * Tracking router events
   */
  public trackRouterEvents(): void {
    /** Triggered when current page is loaded */
    this.routes.events.subscribe((event) => {
      /** Triggered when NavigationEnd event occurs */
      if (event instanceof NavigationEnd) {
        if (this.navigateOn !== event.url) {
          this.analyticsPushData(event);
          this.navigateOn = event.url;
        }
      } else if (event instanceof NavigationError) {
        /** Triggered when NavigationError event occurs */
        this.analyticsPushData(event);
      }
    });
  }

  /**
   * Pushing analytics data
   * @param event - Router Event
   */
  public analyticsPushData(event: any): void {
    const screenshotName = new Date().getTime().toString();
    this.analyticsData = this.analyticsService.setAnalyticsData({}, {}, this.eventLabels.PAGE_LOAD, `${screenshotName}.html`,
      { eventComponent: event.url });
    this.waitTillPageLoad(screenshotName);
    // Data is send only when user configure the page loading to be true
    this.dataStorage.setUrlKey(this.analyticsData.eventComponent);
    setTimeout(() => {
      this.dataStorage.appendToAnalyticsArray(this.analyticsData);
    }, 0);
  }


  /**
   * Waiting for page to load completely
   * @param event 
   */
  waitTillPageLoad(screenshotName: string) {
    const _self = this;
    const interval = setInterval(function () {
      if (this.document.readyState === 'complete') {
        clearInterval(interval);
        _self.captureTemplate(screenshotName);
      }
    }, 1000);
  }

  /**
   * Capture template of loaded view
   * @param screenshotName - Screenshot image
   */
  captureTemplate(screenshotName: string): void {
    const fullPageHTML = ngS3AnalyticsJS.constructHTMLPage(
      this.processRegexOperations(this.document.head.innerHTML),
      this.processRegexOperations(this.document.body.innerHTML)
    );
    this.analyticsService.saveScreenshotsInS3(fullPageHTML, screenshotName);
  }


  processRegexOperations(text: string): string {
    return ngS3AnalyticsJS.doRegex(text, window.location.origin);
  }
}
